.product-card{
    width: 100%;
    overflow: hidden;
    position: relative;
}
.product-img{
    width: 100%;
    position: relative;
    z-index: 2;
    margin-right: 4px;
}
.product-img img{
    /* border-top-left-radius: 50px; */
    background: #ecf0f1;
    padding: 8%;
    transition: all ease 400ms;
}
.product-card:hover .product-name-box{
    background: black;
}
.product-card:hover .product-name{
    color: white;
}
.product-card:hover .product-img img{
    background: white;
}
.product-name-box{
    display: block;
    padding: 25px  35px;
    background-color: #fff;
    box-shadow: 0px 4px 16px 0px rgb(0 0 0 / 6%);
    margin-bottom: 10px;
    transition: all ease 1000ms;
}
.product-name-title{
    font-size: 11px;
    color: var(--primry-clr);
    font-weight: 600;
    text-decoration: none;
    display: block;
    text-transform: uppercase;
}
.product-name{
    font-size: 19px;
    color: black;
    font-weight: 600;
    text-decoration: none;
    display: block;
    position: relative;
    transition: all 600ms;
}
.product-name::before{
    position: absolute;
    content: "";
    top: 36%;
    width: 21px;
    height: 2px;
    left: -35px;
    background-color: var(--primry-clr);
    box-shadow: -6px 5px 0px var(--primry-clr);
}